.message {
    background-color: chartreuse;
    width: 550px;
    height: 125px;
    padding: 15px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    margin: 50px;
}


.avatar {
    flex: 0 0 75px;
    height: 75px;
    margin: 0px 30px 0px 0px;


}


.avatar img {
    width: 100%;
    border-radius: 50%;
}


.main {
    background-color: black;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    padding: 10px 10px 10px 20px;
    box-sizing: border-box;
    position: relative;

}
.main::before {
    content: "";

    border: 50px solid transparent; border-bottom: 50px solid black;
    bottom: 0;
    left: -29px;
    position: absolute;
}



.name {
    color: blueviolet;
    font-size: 40px;
}

.press {
    font-size: 25px;
    font-weight: 700;
    color: antiquewhite;
    position: relative;
    z-index: 4;
}

.line {
    display: flex;
    justify-content: space-between;
}


.time {
    font-size: 40px;
    font-weight: 700;
    color: antiquewhite;
    opacity: 0.5;
}